<script>
import { ref, onMounted, onUnmounted } from 'vue';
import { gsap, Expo } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
gsap.registerPlugin(ScrollTrigger);
export default {
    name: 'ClientCard',
    props: {
    },


    setup() {
     
        const cardListShow = () => {
            console.log( "?" );
            const clientItems = gsap.utils.toArray(".figure_item");
            clientItems.forEach((item, index) => {
                let tl = gsap.timeline({
                    scrollTrigger: {
                        trigger: item,
                        // markers:true,
                        start: "top +=800",
                        // end: "+=500",
                    }
                });
                tl.set(item, { y: 0, opacity: 0 });
                tl.to(item, 0.75, { scale: 1, opacity:1, ease: Expo.easeInOut, delay: index * 0.02});
            })
        }
        onMounted(() => {
            cardListShow();
        })

        onUnmounted(() => {
            cardListShow();
        })

        return {
            // cardListShow
        };
    }
}
</script>
      
<style lang="scss" scoped>
@import "@/assets/scss/__variables.scss";

.clent__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    li {
        position: relative;
        @include breakDevicePoint(width, 20%, 33.3333%, null);
        overflow: hidden;
        @include breakDevicePoint(margin,  vw(10px), 0, null);
        figure {
            width: 100%;

            >img {
                width: 100%;
                opacity: 0;
                transform: scale(.5);
            }
        }
    }
}</style>
      